.how-it-works {
  background: linear-gradient(135deg, #ffffff 0%, #f8f9fa 100%);
  border-radius: 24px; /* Скругленные углы */
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.section-header {
  text-align: center;
  margin-bottom: 60px;
}

.section-badge {
  display: inline-block;
  background: rgba(76, 175, 80, 0.1);
  padding: 10px 20px;
  color: var(--primary-dark);
  font-weight: 600;
  font-size: 0.875rem;
  border-radius: 100px;
  margin-bottom: 32px;
}

.section-description {
  font-size: 1.25rem;
  max-width: 600px;
  margin: 16px auto 0;
}

.steps-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
  position: relative;
  margin-bottom: 60px;
}

.step-card {
  background: white;
  padding: 40px 24px;
  border-radius: 24px;
  text-align: center;
  position: relative;
  box-shadow: 0 10px 30px rgba(0,0,0,0.05);
  transition: transform 0.3s;
}

.step-card:hover {
  transform: translateY(-5px);
}

.step-number {
  position: absolute;
  top: -10px;
  left: 20px;
  background: var(--primary);
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 1.25rem;
}

.step-icon {
  font-size: 3rem;
  margin-bottom: 20px;
}

.step-card h3 {
  margin-bottom: 12px;
}

.step-card p {
  font-size: 1rem;
  margin: 0;
}

.step-connector {
  position: absolute;
  top: 50%;
  right: -30px;
  font-size: 2rem;
  color: var(--border);
  transform: translateY(-50%);
  z-index: 2;
}

.time-guarantee {
  background: white;
  padding: 32px;
  border-radius: 16px;
  display: flex;
  align-items: center;
  gap: 24px;
  max-width: 500px;
  margin: 0 auto;
  box-shadow: 0 5px 20px rgba(0,0,0,0.05);
  border: 1px solid var(--border);
}

.time-guarantee-icon {
  font-size: 3rem;
}

.time-guarantee-content h4 {
  font-size: 1.25rem;
  margin-bottom: 4px;
}

.time-guarantee-content p {
  margin: 0;
  color: var(--primary);
  font-weight: 500;
}

@media (max-width: 768px) {
  .steps-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .step-connector {
    display: none;
  }
}