.form-section {
  background: linear-gradient(135deg, #ffffff 0%, #f8f9fa 100%);
  border-radius: 24px; /* Скругленные углы */
}

.form-card {
  max-width: 800px;
  margin: 0 auto;
  background: white;
  border-radius: 32px;
  padding: 48px;
  box-shadow: 0 20px 60px rgba(0,0,0,0.1);
}

.form-header {
  text-align: center;
  margin-bottom: 40px;
}

.form-subtitle {
  font-size: 1.125rem;
  margin: 36px 0;
}

.spots-left {
  margin-top: 20px;
}

.spots-badge {
  background: linear-gradient(135deg, #FF6B6B, #FF5252);
  color: white;
  padding: 8px 24px;
  border-radius: 100px;
  font-weight: 600;
  font-size: 0.875rem;
  display: inline-block;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.05); }
  100% { transform: scale(1); }
}

.early-access-form {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 24px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.form-group label {
  font-weight: 600;
  font-size: 0.875rem;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.form-group input,
.form-group select {
  padding: 14px 16px;
  border: 2px solid var(--border);
  border-radius: 12px;
  font-size: 1rem;
  transition: all 0.3s;
}

.form-group input:focus,
.form-group select:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 4px rgba(76, 175, 80, 0.1);
}

.form-group input.error,
.form-group select.error {
  border-color: var(--error);
}

.error-message {
  color: var(--error);
  font-size: 0.875rem;
}

.radio-group {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  padding: 8px 0;
}

.radio-label {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
  font-size: 0.95rem;
}

.radio-label input[type="radio"] {
  width: 18px;
  height: 18px;
  accent-color: var(--primary);
}

.price-range-group {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 12px;
  margin-top: 8px;
}

.price-label {
  position: relative;
  cursor: pointer;
}

.price-label input[type="radio"] {
  position: absolute;
  opacity: 0;
  width: 0;
  height: 0;
}

.price-button {
  display: block;
  padding: 12px;
  text-align: center;
  border: 2px solid var(--border);
  border-radius: 50px;
  font-weight: 500;
  transition: all 0.3s;
}

.price-label input[type="radio"]:checked + .price-button {
  background: var(--primary);
  color: white;
  border-color: var(--primary);
}

.submit-button {
  background: var(--primary);
  color: white;
  padding: 18px 32px;
  border-radius: 50px;
  font-weight: 700;
  font-size: 1.125rem;
  border: none;
  cursor: pointer;
  transition: all 0.3s;
  margin-top: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
}

.submit-button:hover:not(:disabled) {
  background: var(--primary-dark);
  transform: translateY(-2px);
  box-shadow: 0 10px 30px rgba(76, 175, 80, 0.4);
}

.submit-button:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.spinner {
  width: 20px;
  height: 20px;
  border: 3px solid rgba(255,255,255,0.3);
  border-radius: 50%;
  border-top-color: white;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

.form-note {
  text-align: center;
  font-size: 0.875rem;
  color: var(--text-secondary);
  margin-top: 16px;
}

@media (max-width: 768px) {
  .form-card {
    padding: 32px 20px;
  }
  
  .form-row {
    grid-template-columns: 1fr;
    gap: 16px;
  }
  
  .price-range-group {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .radio-group {
    flex-direction: column;
    gap: 12px;
  }
}