* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  color: var(--text-primary);
  line-height: 1.5;
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1;
}

section {
  padding: 60px 20px;
  max-width: 1200px;
  margin: 0 auto;
}

h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

h2 {
  font-size: 2rem;
  margin-bottom: 2rem;
  text-align: center;
}

h3 {
  font-size: 1.5rem;
  font-weight: 600;
}

button {
  cursor: pointer;
  border: none;
  background: none;
  font-size: 1rem;
}

.cta-button {
  background-color: #4CAF50;
  color: white;
  padding: 15px 40px;
  border-radius: 50px;
  font-size: 1.2rem;
  font-weight: bold;
  transition: background-color 0.3s;
  border: none;
  cursor: pointer;
}

.cta-button:hover {
  background-color: #45a049;
}

footer {
  text-align: center;
  padding: 30px;
  background-color: #f5f5f5;
  color: #666;
}

:root {
  /* Основные цвета */
  --primary: #4CAF50;      /* Свежий зелёный - как у Instacart */
  --primary-dark: #2E7D32;  /* Тёмно-зелёный для ховеров */
  --primary-light: #C8E6C9; /* Светло-зелёный для фонов */
  
  --secondary: #FF6B6B;     /* Коралловый для акцентов (бонусы, скидки) */
  
  /* Нейтральные */
  --text-primary: #1A1A1A;   /* Почти чёрный для заголовков */
  --text-secondary: #666666; /* Серый для описаний */
  --background: #FFFFFF;     /* Белый фон */
  --background-light: #F8F9FA; /* Светло-серый для секций */
  --border: #E0E0E0;        /* Цвет границ */
  
  /* Системные */
  --success: #4CAF50;
  --error: #F44336;
  --warning: #FF9800;
}

p {
  font-size: 1.125rem;
  color: var(--text-secondary);
}

/* Плавный скролл */
html {
  scroll-behavior: smooth;
}

/* Анимация появления элементов */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

section {
  animation: fadeInUp 0.6s ease-out;
}

/* Кастомный скроллбар */
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: var(--primary);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--primary-dark);
}
