.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background: transparent;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  padding: 20px 0;
}

.header-scrolled {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  padding: 12px 0;
}

.header-visible {
  transform: translateY(0);
}

.header-hidden {
  transform: translateY(-100%);
}

.header-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 24px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

/* Логотип */
.header-logo {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
  transition: transform 0.3s;
}

.header-logo:hover {
  transform: scale(1.05);
}

.logo-icon {
  font-size: 2rem;
}

.logo-text {
  font-size: 1.5rem;
  font-weight: 700;
  background: linear-gradient(135deg, var(--primary) 0%, var(--primary-dark) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* Навигация */
.header-nav {
  display: flex;
  gap: 32px;
}

.nav-link {
  background: none;
  border: none;
  font-size: 1rem;
  font-weight: 500;
  color: var(--text-primary);
  cursor: pointer;
  padding: 8px 0;
  position: relative;
  transition: color 0.3s;
}

.header-scrolled .nav-link {
  color: var(--text-primary);
}

.nav-link:hover {
  color: var(--primary);
}

.nav-link::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: var(--primary);
  transform: scaleX(0);
  transition: transform 0.3s;
}

.nav-link:hover::after,
.nav-link.active::after {
  transform: scaleX(1);
}

.nav-link.active {
  color: var(--primary);
  font-weight: 600;
}

/* Кнопка раннего доступа */
.query-form-button {
  background: var(--primary);
  color: white;
  border: none;
  padding: 10px 24px;
  border-radius: 50px;
  font-weight: 600;
  font-size: 0.95rem;
  cursor: pointer;
  transition: all 0.3s;
  white-space: nowrap;
}

.query-form-button:hover {
  background: var(--primary-dark);
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(76, 175, 80, 0.3);
}

/* Кнопка мобильного меню */
.mobile-menu-button {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 21px;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
  z-index: 1002; /* Выше чем мобильное меню */
  position: relative;
}

.mobile-menu-button span {
  width: 100%;
  height: 3px;
  background: var(--text-primary);
  border-radius: 3px;
  transition: all 0.3s;
}

.mobile-menu-button.open span:nth-child(1) {
  transform: translateY(9px) rotate(45deg);
}

.mobile-menu-button.open span:nth-child(2) {
  opacity: 0;
}

.mobile-menu-button.open span:nth-child(3) {
  transform: translateY(-9px) rotate(-45deg);
}

/* Мобильное меню */
.mobile-menu {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: white;
  z-index: 1000;
  flex-direction: column;
  align-items: center;
  justify-content: center; /* Центрируем по вертикали */
  transform: translateX(100%);
  transition: transform 0.3s ease-in-out;
  /* Убираем фиксированную высоту и скролл */
  height: auto; /* Высота по контенту */
  min-height: 30vh; /* Минимальная на весь экран, но может быть больше */
  padding: 20px; /* Уменьшил padding для гибкости */
  box-sizing: border-box;
  gap: 12px; /* Уменьшил gap */
}

/* Добавляем отдельный слой фона для надежности */
.mobile-menu::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: white;
  z-index: -1;
}

.mobile-menu.open {
  transform: translateX(0);
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}

.mobile-nav-link {
  background: none;
  border: none;
  font-size: clamp(1.2rem, 5vw, 1.8rem); /* Адаптивный размер */
  font-weight: 600;
  color: var(--text-primary);
  cursor: pointer;
  padding: 15px 0;
  width: 100%;
  max-width: 300px; /* Ограничиваем ширину */
  text-align: center;
  transition: all 0.3s;
  border-bottom: 2px solid transparent;
  /* Добавляем фон при наведении */
  border-radius: 8px;
  /* Сжимается если не влезает */
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  box-sizing: border-box;

}

.mobile-nav-link:hover,
.mobile-nav-link.active {
  color: var(--primary);
  border-bottom-color: var(--primary);
  background-color: rgba(76, 175, 80, 0.05); /* Легкий фон при наведении */
}

/* Плейсхолдер для отступа контента */
.header-placeholder {
  height: 80px;
}

/* Адаптивность */
@media (max-width: 768px) {
  .header-nav {
    display: none;
  }

  .query-form-button {
    display: none;
  }

  .mobile-menu-button {
    display: flex;
  }

  .mobile-menu {
    display: flex;
  }

  .header-container {
    padding: 0 20px;
  }
}

@media (min-width: 769px) and (max-width: 1024px) {
  .header-nav {
    gap: 20px;
  }

  .nav-link {
    font-size: 0.95rem;
  }
}

/* Для очень маленьких экранов */
@media (max-height: 500px) {
  .mobile-menu {
    justify-content: flex-start; /* Прижимаем к верху */
    padding-top: 60px;
  }
  
  .mobile-nav-link {
    padding: 6px 12px;
    font-size: 1.1rem;
  }
  
  .mobile-early-access {
    padding: 8px 16px;
    font-size: 0.9rem;
    margin-top: 10px;
  }
}

/* Для очень широких но низких экранов */
@media (max-height: 400px) and (min-width: 600px) {
  .mobile-menu-content {
    flex-direction: row; /* Горизонтальное меню */
    flex-wrap: wrap;
    gap: 10px;
  }
  
  .mobile-nav-link {
    width: auto;
    flex: 0 1 auto;
  }
  
  .mobile-early-access {
    width: auto;
    margin-top: 0;
  }
}