.thank-you-section {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 24px;
  background: linear-gradient(135deg, #f5f7fa 0%, #e9ecef 100%);
  border-radius: 24px; /* Скругленные углы */
}

.thank-you-card {
  max-width: 600px;
  background: white;
  border-radius: 32px;
  padding: 60px 40px;
  text-align: center;
  box-shadow: 0 20px 60px rgba(0,0,0,0.1);
}

.success-animation {
  margin: 0 auto 40px;
  width: 100px;
  height: 100px;
}

.checkmark {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  stroke: var(--primary);
  stroke-width: 2;
  stroke-miterlimit: 10;
  animation: fill 0.4s ease-in-out 0.4s forwards, scale 0.3s ease-in-out 0.9s both;
}

.checkmark-circle {
  stroke-dasharray: 166;
  stroke-dashoffset: 166;
  stroke-width: 2;
  stroke-miterlimit: 10;
  stroke: var(--primary);
  fill: none;
  animation: stroke 0.6s cubic-bezier(0.65, 0, 0.45, 1) forwards;
}

.checkmark-check {
  transform-origin: 50% 50%;
  stroke-dasharray: 48;
  stroke-dashoffset: 48;
  animation: stroke 0.3s cubic-bezier(0.65, 0, 0.45, 1) 0.8s forwards;
}

@keyframes stroke {
  100% { stroke-dashoffset: 0; }
}

.thank-you-message {
  font-size: 1.25rem;
  margin: 24px 0;
  line-height: 1.6;
}

.whats-next {
  background: var(--background-light);
  padding: 32px 24px;
  border-radius: 20px;
  margin: 40px 0;
  text-align: left;
}

.whats-next h3 {
  margin-bottom: 20px;
  color: var(--primary-dark);
}

.whats-next ul {
  list-style: none;
  padding: 0;
}

.whats-next li {
  margin: 16px 0;
  font-size: 1.1rem;
  display: flex;
  align-items: center;
  gap: 12px;
}

.social-share {
  margin: 40px 0;
}

.share-buttons {
  display: flex;
  gap: 12px;
  justify-content: center;
  margin-top: 16px;
  flex-wrap: wrap;
}

.share-button {
  padding: 10px 20px;
  border-radius: 50px;
  color: white;
  text-decoration: none;
  font-weight: 600;
  transition: transform 0.3s;
}

.share-button:hover {
  transform: translateY(-2px);
}

.telegram { background: #0088cc; }
.whatsapp { background: #25D366; }
.facebook { background: #1877f2; }

.back-to-top {
  background: none;
  border: 2px solid var(--border);
  padding: 12px 32px;
  border-radius: 50px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s;
  margin-top: 20px;
}

.back-to-top:hover {
  border-color: var(--primary);
  color: var(--primary);
}